body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*
=========================================
=========================================

  Appton - Bootstrap 4 Template

=========================================
========================================= */
/*
* =====================================================
*     GENERAL
* =====================================================
*/
.text-bold {
  font-weight: 700;
}

.text-small {
  font-size: 0.9rem;
}

.btn-outline-light:hover {
  color: aqua !important;
}

section {
  padding: 7rem 0;
}

strong {
  font-weight: 600;
}

a {
  transition: all 0.3s;
  display: inline-block;
}

.modal {
  background: rgba(0, 0, 0, 0.85);
}

.modal-content {
  border-radius: 0;
}

.modal-content .close {
  width: 3rem;
  height: 3rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: aqua;
  display: block;
  opacity: 1;
  padding: 0;
  outline: none;
}

.modal-content .close span {
  color: #fff;
}

button {
  cursor: pointer;
  transition: all 0.3s;
}

.bg-gray, .sidebar-widget {
  background: #f5f5f5 !important;
}

.breadcrumb {
  background: none;
}

.breadcrumb li.breadcrumb-item {
  color: #999;
}

.form-group label {
  color: #555;
  font-weight: 400;
  font-size: 1.05rem;
}

.form-group label span.icon {
  width: 2rem;
  height: 2rem;
  line-height: 2rem;
  background: aqua;
  color: #000;
  text-align: center;
  display: inline-block;
  margin-right: 0.5rem;
  border-radius: 50%;
  font-size: 0.85rem;
}

.form-group textarea {
  min-height: 10rem;
}

.form-control::-moz-placeholder {
  font-size: 0.95rem;
  color: #aaa !important;
}

.form-control::-webkit-input-placeholder {
  font-size: 0.95rem;
  color: #aaa !important;
}

.form-control:-ms-input-placeholder {
  font-size: 0.95rem;
  color: #aaa !important;
}

.breadcrumb-item + .breadcrumb-item::before {
  font-size: 0.8rem;
}

/* GENERAL MEDIAQUERY */
@media (max-width: 991.98px) {
  section {
    padding: 5rem 0;
  }
}

/*
* =====================================================
*     NAVBAR
* =====================================================
*/
nav.navbar {
  transition: all 0.4s;
}

nav.navbar.active {
  background: #fff;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.08);
}

nav.navbar .dropdown-menu {
  margin-top: 1.4rem;
}

/* NAVBAR MEDIAQUERY */
@media (max-width: 991.98px) {
  nav.navbar {
    background: #fff;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.08);
  }
  nav.navbar .dropdown-menu {
    box-shadow: none;
    margin-top: 0;
    margin-bottom: 1rem;
  }
}

/*
* =====================================================
*     FOOTER
* =====================================================
*/
footer.main-footer {
  padding: 7rem 0;
  background: aqua;
  color: #000;
  padding-bottom: 0;
}

.footer-logo {
  width: 7.5rem;
}

.footer-link {
  color: #000;
  font-size: 0.9rem;
  font-weight: 300;
  margin: 0.3rem 0;
}

.footer-link:hover, .footer-link:focus {
  color: #000;
}

.social-link {
  color: #fff;
  font-size: 0.9rem;
  margin: 0 0.625rem;
}

.social-link:hover {
  color: #000;
}

.footer-heading {
  margin-bottom: 1.25rem;
  font-weight: 600;
}

.copyrights-text {
  font-weight: 300;
  font-size: 0.9rem;
}

.copyrights {
  margin-top: 2rem;
  padding: 1.25rem 0;
}

/*

=====================
STYLE SWITCHER FOR DEMO
=====================

*/
#style-switch-button {
  position: fixed;
  top: 120px;
  left: 0px;
  border-radius: 0;
  z-index: 2;
}

#style-switch {
  width: 300px;
  padding: 20px;
  position: fixed;
  top: 160px;
  left: 0;
  background: #000;
  border: solid 1px #ced4da;
  z-index: 2000;
}

#style-switch h4 {
  color: #495057;
}

#style-switch select {
  height: 50px;
}

/* =====================================================
*     HERO SECTION
* =====================================================
*/
.hero {
  padding: 5rem 0;
  /* background: url(./img/hero-bg.svg) no-repeat; */
  background-size: cover;
  background-position: bottom left;
}

.subscription-form .form-group {
  position: relative;
}

.subscription-form .form-group button {
  position: absolute;
  top: 0.375rem;
  right: 0.4rem;
  border-radius: 0;
}

.subscription-form input {
  height: auto;
}

.subscription-form input::-moz-placeholder {
  font-weight: 300;
  font-size: 0.9rem;
  color: #aaa;
}

.subscription-form input::-webkit-input-placeholder {
  font-weight: 300;
  font-size: 0.9rem;
  color: #aaa;
}

.subscription-form input:-ms-input-placeholder {
  font-weight: 300;
  font-size: 0.9rem;
  color: #aaa;
}

/* HERO MEDIAQUERY */
@media (max-width: 991.98px) {
  .hero {
    padding: 5rem 0 7rem;
    padding-top: 8rem;
  }
  .subscription-form .form-group {
    position: static;
  }
  .subscription-form .form-group button {
    position: static;
    width: 100%;
    margin-top: 0.5rem;
  }
}

/*
* =====================================================
*     INTEGRATIONS SECTION
* =====================================================
*/
.box {
  padding: 3rem 2rem;
  transition: all 0.4s;
}

.box:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.box .icon {
  width: 2.8rem;
  height: 2.8rem;
  margin: 0 auto;
  margin-bottom: 1rem;
}

/*
* =====================================================
*     CLIENTS SECTION
* =====================================================
*/
.clients img {
  max-height: 5rem;
  padding: 0.6rem;
}

/*
* =====================================================
*     PORTFOLIO SECTION
* =====================================================
*/
.portfolio-item {
  position: relative;
  display: block;
  overflow: hidden;
}

.portfolio-item::after {
  content: '';
  width: 120%;
  height: 120%;
  background: radial-gradient(circle closest-corner, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
  display: block;
  position: absolute;
  top: -10%;
  left: -10%;
}

/*
* =====================================================
*     CONTACT SECTIOn
* =====================================================
*/
.contact-details .box {
  padding: 1.5rem;
  box-shadow: none;
  text-align: center;
}

.contact-details .icon {
  width: 1.8rem;
  height: 1.8rem;
}

