/* Top Navigation Style */
.logreg-links {
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	margin: 0 1em 0 0;
	text-align: center;
	white-space: nowrap;
}

.logreg-icon {
	display: inline-block;
}

.logreg-icon:first-child {
	margin-right: 0.5rem;
}

.canvas-wrap {
